import { words } from './Words';
import { sentences } from './Sentences';
const QuestionSource = { words, sentences };
export const getQuestions = () => {
    fetch('/projects/english-learner/source/words.json').then(res => {
        res.json().then(val => { QuestionSource.words = val; }).catch(err => { console.log(err); });
    }).catch(err => { console.log(err); });
    fetch('/projects/english-learner/source/sentences.json').then(res => {
        res.json().then(val => { QuestionSource.sentences = val; }).catch(err => { console.log(err); });
    }).catch(err => { console.log(err); });
};
export const getRandomQuestion = () => {
    const qList = []; const count = 4;
    const types = ['words', 'sentences'];
    const type = types[Math.floor(Math.random() * types.length)];
    let q = QuestionSource[type][Math.floor(Math.random() * QuestionSource[type].length)];
    for (let i = 0; i < count; i++) {
        while (qList.includes(q)) {
            q = QuestionSource[type][Math.floor(Math.random() * QuestionSource[type].length)];
        }
        qList.push(q);
    }
    return { qList, q: Math.floor(Math.random() * count) };
}
export const findQuestion = (key = 'Walnut', type = 'words') => {
    const qList = []; const count = 4;
    let question = QuestionSource[type].find((a) => {
        if (qList.length < count) { qList.push(a); }
        return a.en === key;
    });
    if (!question) {
        question = { type, en: 'Not found', tr: 'Bulunamadı' };
    }
    const q = qList.length - 1;
    qList[q] = question;
    return { qList, q };
}